import { Controller, hotkeys } from '@ali/4ever-cangjie';
import * as React from 'react';
import onEnter from './onEnter';

export default function onKeyDown(event: React.KeyboardEvent, controller: Controller, next: Function) {
  if (hotkeys.isEnter(event)) {
    return onEnter(event, controller, next);
  }

  return next();
}
